        padding: 40px 0;
        @include mq(kadel-lt-lg) {
          max-width: 95%;
          margin: 0 auto;
        }
        @include mq(kadel-lg) {
          max-width: 100%;
        }
      }

      .close {
        position: absolute;
        right: 2.5%;
        padding: 0;
        border-style: none;
        background-color: transparent;
        font-size: 28px;
        color: #000;
        opacity: 0.65;
        height: 44px;
        line-height: 44px;
        user-select: none;
        outline: 0;
        cursor: zoom-out;
        @include mq(kadel-lg) {
          right: 0;
        }
        &:hover {
          opacity: 1;
        }
      }
    }
  }

  /* ---------------------------
            p-main
  ------------------------------ */
  .p-main {
    @include mq(kadel-gt-md) {
      display: flex;
      flex-direction: row-reverse;
      height: calc(100vh - 90px);
      width: 100%;
    }

    &__figure {
      @include mq(kadel-gt-md) {
        background: url('../assets/images/kadel/hero_img.jpg') no-repeat
          center/cover;
        transition: width 1.4s cubic-bezier(0.86, 0, 0.07, 1);
        width: 100%;
        transform: translate3D(0, 0, 0);
      }
    }

    &__text {
      display: flex;
      flex-direction: column;
      justify-content: center;
      -ms-writing-mode: tb-rl;
      writing-mode: vertical-rl;
      position: relative;
      @include mq(kadel-sm) {
        align-items: center;
        width: 100%;
        height: calc(100vh - 70px);
        background: url('../assets/images/kadel/hero_img-sp.jpg') no-repeat
          center/cover;
        color: #fff;
      }
      @include mq(kadel-gt-md) {
        justify-content: flex-end;
        padding-top: 120px;
      }

      > h2 {
        font-size: 32px;
        line-height: 1.5;
        @include mq(kadel-sm) {
          letter-spacing: 1.4px;
        }
      }

      > h3 {
        font-size: 20px;
        @include mq(kadel-sm) {
          padding-top: 6em;
          margin-left: -1.5em;
          margin-right: 0.5em;
        }
        @include mq(kadel-gt-md) {
          text-align: right;
          margin: 0 1em 0 1.5em;
        }
      }
    }

    &__text.is-loadedWidth {
      @include mq(kadel-gt-md) {
        width: 17.6%;
      }
    }

    &__scrollBtn {
      position: absolute;
      bottom: 0;
      right: 0;
      font-size: 12px;
      font-weight: 400;
      font-family: 'Ubuntu Condensed', sans-serif;
      padding-bottom: 168px;
      z-index: 900;
      color: #fff;
      @include mq(kadel-gt-md) {
        color: #262626;
      }

      &:hover {
        opacity: 0.7;
      }

      > span {
        position: absolute;
        bottom: 0;
        left: 50%;
        content: '';
        height: 140px;
        width: 1px;
        overflow: hidden;

        &:after {
          content: '';
          display: block;
          height: 160px;
          width: 1px;
          position: absolute;
          background-color: #fff;
          -webkit-animation-duration: 2.6s;
          animation-duration: 2.6s;
          -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
          -webkit-animation-name: scrollLine;
          animation-name: scrollLine;
          -webkit-animation-timing-function: cubic-bezier(
            0.97,
            0.02,
            0.59,
            0.98
          );
          animation-timing-function: cubic-bezier(0.97, 0.02, 0.59, 0.98);
          @include mq(kadel-gt-md) {
            background-color: #262626;
          }
        }

        @keyframes scrollLine {
          0% {
            -webkit-transform: translateY(-100%);
          }
          5% {
            -webkit-transform: translateY(-100%);
          }
          25% {
            -webkit-transform: translateY(0);
          }
          55% {
            -webkit-transform: translateY(1%);
          }
          95% {
            -webkit-transform: translateY(100%);
          }
          100% {
            -webkit-transform: translateY(100%);
          }
        }
      }
    }
  }

